@import 'variables';

*, ::after, ::before {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
}

html {
    scroll-behavior: smooth;
}

body {
    padding: 0;
    margin: 0;
    background: $white-color;

}

p {
    margin-bottom: 0;
}

img {
    border: none;
}

header, footer {
    margin: auto;
    max-width: 1920px;
}

main {
    margin: auto;
}

a {
    text-decoration: none;
    cursor: pointer;
}

button {
    border: none;
    cursor: pointer;
}

ul {
    list-style: none;
}


.container {
    padding-right: 15px;
    padding-left: 15px;
    margin-right: auto;
    margin-left: auto;
    border: 1px solid pink;
}




.delivery-title {
    padding-top: 40px;
    margin-bottom: 25px;
    padding-bottom: 5px;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-weight: 400;
    font-size: 14px;
    line-height: 150%;
    color: #7c7c7c;
    border-bottom: 1px solid #eaeaea;
}

.corsik_yaDeliveryMap {
   
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-weight: 400;
    display: grid;
    gap: 30px;
    grid-template-columns: 1fr;
    grid-template-areas: 
    "address"
    "map"
    "options";
   
    &__map {
        grid-area: map;
        background: #eeeeee;
        height: min(66vw,  465px);

        //надпись для обозначения карты
        //удалить
        display: flex;

        //удалить
        &::before {
            content: 'Здесь будет карта';
            color: black;
            font-size: 30px;
            display: block;
            margin: auto;

        }

    }

    &__address {
        grid-area: address;
    }

    &__address-header {
        margin-bottom: 30px;
        width: 60%;
        font-weight: 500;
        font-size: 28px;
        line-height: 1;
        color: #000;
    }

    &__options {
        grid-area: options;

        margin-bottom: 40px;
    }

    &__address-title {
        margin-bottom: 10px;
        font-weight: 500;
        font-size: 16px;
        line-height: 150%;
        color: #000;
    }

    &__input {
        padding: 13px 11px;
        border: 1px solid #eee;
        width: 100%;

        &::placeholder {
            font-weight: 400;
            font-size: 14px;
            line-height: 150%;
            color: #c6c6c6;
            opacity: 0;
        }
    }

    &__radioBlock {
        padding-bottom: 30px;
        margin-bottom: 30px;
        display: flex;
        flex-wrap: wrap;
        gap: 40px;
        border-bottom: 1px solid #eaeaea;
    }

    &__cost-title {
        margin-bottom: 30px;
        font-weight: 400;
        font-size: 12px;
        line-height: 150%;
        text-transform: uppercase;
        color: #7c7c7c;
    }

    &__cost-items {
        display: flex;
        flex-wrap: wrap;
        gap: 45px;
    }

    &__cost-text {
        font-size: 14px;
        line-height: 150%;
        color: #7c7c7c;
        display: inline-flex;
        align-items: center;
        gap: 5px;

        img {
            flex-shrink: 0;
            cursor: pointer;
        } 
    }

    &__cost-value {
        font-weight: 500;
        font-size: 16px;
        line-height: 150%;
        color: #000;
    }
}

.radio {
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-weight: 400;
    font-size: 14px;
    line-height: 150%;
    color: #7c7c7c;
    cursor: pointer;
    min-width: 120px;


    &__text {
        margin-bottom: 33px;
    
    }

    &__car {
        margin-bottom: 5px;

        path {
            transition: all .3s ease-in-out;
        }
    }

    &__input {
        display: none;
    }

    &__info {
        position: relative;
        padding-left: 26px;
        display: inline-block;
        font-size: 14px;
        transition: all .3s ease-in-out;

        &::before {
            content: '';
            position: absolute;
            display: inline-block;
            top: 0;
            left: 0;
            width: 18px;
            height: 18px;
            border: 1px solid #eaeaea;
            border-radius: 50%;
            transition: all .3s ease-in-out;
        }

        &::after {
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            transform: translate(64%, 64%);
            height: 8px;
            width: 8px;
            background: #A91D2D;
            opacity: 0;
            border-radius: 50%;
            transition: all .3s ease-in-out;
        }

    }
    &__input:checked + &__info::before {
        border-color: #A91D2D;
    }

    &__input:checked + &__info::after {
        opacity: 1;
    }


    &__input:checked + &__info {
        color: black;

        .radio__car {
            path {
                fill: black;
            }
        } 
    }
}


//media-queries
@media (min-width: 768px) {
    .container {
        width: 750px
    }

   

    .corsik_yaDeliveryMap {
        margin-bottom: 80px;

        &__map {
            height: 465px;
        }

        &__address-header {
            margin-bottom: 30px;
            width: 100%;
        }

        &__input { 
            padding: 12px 11px;
            width: 80%;

            
        &::placeholder {
            
            opacity: 1;
        }
        }
    }
}

@media (min-width: 992px) {
    .container {
        width: 970px
    }
}

@media (min-width: 1230px) {
    .container {
        width: 1200px
    }

    .delivery-title {
        width: calc(50% - 15px);
        margin-left: auto;
        margin-bottom: 30px;
    }

    .corsik_yaDeliveryMap {
        grid-template-columns: 1fr 1fr;
        // gap: 30px;
        grid-template-areas: 
        "map address"
        "map options";

        &__options {
            margin-bottom: 46px;
        }
    }
}